<template>
    <v-container fluid>
        
        <ah-one-header :me="mine"></ah-one-header>

        <qiong-page-panel>
            <ah-one-table-header v-if="index > 0"></ah-one-table-header>
            <div class="qiong-pt-5"></div>
            <ah-one-table-body :index="i" :me="mine" :childs="childrens" v-if="childrens != null"></ah-one-table-body>
            <div class="qiong-bottom-spce"></div>
        </qiong-page-panel>
    </v-container>
</template>

<script>
import api_bonus from '@/common/es/api_bonus/index'

import QiongPagePanel from '../../../../components/Qiong/QiongPagePanel.vue'
import AhOneHeader from './Inner/AhOneHeader.vue'
import AhOneTableHeader from './Inner/AhOneTableHeader.vue'
import AhOneTableBody from './Inner/AhOneTableBody.vue'

    export default {
  components: { QiongPagePanel, AhOneHeader, AhOneTableHeader, AhOneTableBody },
        props: {
            
        },
        name: '',
        inject: [ 'reload' ],
        watch: {
            $route(to, from) {
                if (from.query) {
                    this.mine = null
                    this.childrens = null
                    this.reload()
                }
            }
        },
        data() {
            return {
                id: null,
                i: null,
                mine: null,
                childrens: null
            }
        },
        mounted() {
            this.id = this.$route.query.id
            this.me()
        },
        methods: {
            me(sort = true) {
                const _this = this
                const many = this.$store.state.users_sponser
                const transfer = this.$store.state.transfer

                if (many) {

                    api_bonus._build_res(many, transfer, function (u, t) {

                        if (u.unid == _this.id) {

                            let res = JSON.parse(JSON.stringify(u))
                            _this.mine = res
                        }
                    })
                }
                
                if (this.mine) {
                    this.childrens = [ ]
                    api_bonus._build_res([ this.mine ], transfer, function (u, t) {

                        if (u.unid != _this.mine.unid) {
                            _this.childrens.push(u)

                            if (u.index > _this.i) {
                                _this.i = ( u.index - _this.mine.index )
                            }
                        }
                    })
                }

            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


