<template>
    <div v-if="index > 0">

        <div v-for="(v, i) in childs" :key="i">
            <div class="qiong-td qiong-pb-3 qiong-pt-1 qiong-txt-td">
                <div class="qiong-wide-8">&nbsp;</div>
                <div class="qiong-wide-17">
                    <named-viewing :index="v.index - me.index - 1" :named="v.username" :need_icon="v.has_child"></named-viewing>
                </div>
                <div class="qiong-wide-8">{{ v.number_code }}</div>
                <div class="qiong-wide-28">{{ v.display_name }}</div>
                <div class="qiong-wide-6">{{ v.index - me.index }}</div>
                <div class="qiong-wide-6">{{ v.ipv }}</div>
                <div class="qiong-wide-10">
                    <level-viewing :item="v.vip.name + '_' + v.vip.star"></level-viewing>
                </div>
                <div class="qiong-wide-12">{{ v.bonus.usd_active_house }}</div>
                <div class="qiong-wide-5">
                    <qiong-icon-inset :color="'primary'" :arg="v" @done_Father="openCenter"></qiong-icon-inset>
                </div>
            </div>
        </div>
    
    </div>

    <div v-else class="w-full h-full">
        <qiong-space :space="'4vh'"></qiong-space>
        <qiong-empty></qiong-empty>
    </div>
</template>

<script>
import Loading from '../../../../../components/Data/Loading.vue'
import QiongIconInset from '../../../../../components/Qiong/Button/QiongIconInset.vue'
import QiongEmpty from '../../../../../components/Qiong/Ui/QiongEmpty.vue'
import QiongSpace from '../../../../../components/Qiong/Ui/QiongSpace.vue'
import LevelViewing from '../../../../../components/Viewing/LevelViewing.vue'
import NamedViewing from '../../../../../components/Viewing/NamedViewing.vue'

    export default {
        components: { LevelViewing, QiongIconInset, Loading, QiongEmpty, NamedViewing, QiongSpace },
        inject: [ 'reload' ],
        props: {
            index: {
                type: Number
            },
            me: {
                type: Object
            },
            childs: {
                type: Array
            }
        },
        name: '',
        data() {
            return {
                
            }
        },
        methods: {
            
            openCenter(user) {
                // this.$store.commit('saveUser', user)

                this.$router.push({ path: '/home/bonus/active_house_one/', query: { 'id': user.unid } })
                this.reload()
            }
        },
    }
</script>

<style lang="sass" scoped>
    
</style>


